<template>
	<div class="ScrollY">
		<ul class="ScrollYul" v-if="bookhot">
			<BookItem2 v-for="book in bookhot.books.slice(0,3)"
			:key="book._id"
			:book="book"
			>{{book.latelyFollower|followerCount}}人气</BookItem2>
		</ul>
	</div>
</template>

<script>
	import BookItem2 from "@/components/BookItem2.vue"
	export default{
		components:{
			BookItem2
		},
		props:["bookhot"],
		filters:{
			followerCount:function(value){
				if(value>10000){
					return(value/10000).toFixed(1)+"万";
				}
				return value;
			}
		}
	}
</script>

<style lang="less">
	.ScrollYul{
		
	}
</style>
