<template>
	<div class="Home-Header">
		<van-nav-bar left-arrow>
			<template #left>
				<van-image width="90" height="18" src="https://qidian.gtimg.com/qdm/img/logo-qdm.02fc8.svg" />
			</template>
			<template #title>
				<van-button @click="$router.push('/?gender=male')" :class="{active:gender === 'male'}" plain type="default" size="small">男生</van-button>
				<van-button @click="$router.push('/?gender=female')" :class="{active:gender === 'female'}" plain type="default"
				size="small">女生</van-button>
			</template>
			<template #right>
				<div class="bookshelf" @click="$router.push('/bookshelf')"><img src="../img/书架.png" alt=""></div>
				<van-icon name="data:image/png;base64,"
				size="18" />
			</template>
		</van-nav-bar>

		<van-swipe class="my-swipe" :autoplay="3000" indicator-color="#ed424b">
			<van-swipe-item v-for="(item,index) in (gender==='male'? bannerMale:bannerFemale)" :key="index">
				<van-image width="100%" :src="item.img" />
			</van-swipe-item>
		</van-swipe>

		<!-- <div class="search">
			<van-swipe class="my-swipeinput" vertical :autoplay="3000" :show-indicators='false'>
				<van-swipe-item>1</van-swipe-item>
				<van-swipe-item>2</van-swipe-item>
				<van-swipe-item>3</van-swipe-item>
				<van-swipe-item>4</van-swipe-item>
			</van-swipe>
			<div class="inputfalse"></div>
		</div> -->
	</div>
</template>

<script>
	export default {
		props: ['gender'],
		data: function() {
			return {
				bannerMale: [],
				bannerFemale: []
			}
		},
		created() {
			this.axios.get('/banner.json').then((response) => {
				this.bannerMale = response.data.male;
				this.bannerFemale = response.data.female;
			})
		}

	}
</script>

<style lang="less">
	.Home-Header {
		.van-nav-bar {
			background-color: #f6f7f9;
		}

		.van-nav-bar__title {
			.van-button {
				height: 25px;
				width: 50px;
				background-color: transparent;
				border-width: 1.5px;
				border-color: #f19aa0;
				border-radius: 4px 0 0 4px;
				color: #ed424b;

				&+.van-button {
					border-left: none;
					border-radius: 0 4px 4px 0;
				}

				&.active {
					background-color: #ed424b;
					color: #ffffff;
				}
			}
		}

		.van-nav-bar__right {
			.bookshelf {
				width: 16px;
				padding-top: 5px;
				padding-right: 5px;
				img {
					width: auto;
					height: auto;
					max-width: 100%;
					max-height: 100%;
				}
			}
		}

		.my-swipe {
			width: 336px;
			height: 116px;
			margin: 0 auto;

			.van-swipe__track {
				width: 1344px;
			}

			.van-swipe__indicators {
				.van-swipe__indicator {
					opacity: 1;
					width: 5px;
					height: 5px;
				}
			}

			.van-swipe-item {
				color: #fff;
				font-size: 20px;
				line-height: 116px;
				text-align: center;
				background-color: #39a9ed;

				img {
					width: 100%;
				}
			}
		}

		.search {
			position: relative;

			.my-swipeinput {
				width: 336px;
				height: 32px;
				text-align: center;
				line-height: 32px;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}

			.inputfalse {
				width: 336px;
				height: 32px;
				font-size: 14px;
				line-height: 32px;
				border-radius: 4px;
				background-color: #ffffff;
				color: #969ba3;
				margin: 8px auto;
				text-align: center;

			}
		}

	}
</style>
