<template>
	<li class="BookItem" @click="$router.push('/book/'+book._id)">
		<div class="corner">		
		<span class="corner-rank" v-if="special()">
			<em :class="index+1 | stylehotnum">top{{index+1}}</em>
		</span>
		<img 
		:src="`http://statics.zhuishushenqi.com${book.cover}`" alt="">
	<div class="figcaption">{{book.title}}</div>
	<p class="author">{{book.author}}</p>
	</div>
	</li>
</template>

<script>
	export default{
		props:["index","book","hotid"],
		computed:{
			
		},
		filters:{
			stylehotnum:function(value){
				if (value == 3) {
					return 'yellownum';
				} else if(value == 2){
					return "orangenum";
				} else if(value == 1){
					return "rednum";
				}else{
					return "greynum";
				}
			}
		},
		methods:{
			special:function(){
				// this.hotid==='54d42d92321052167dfb75e3'?false:true;
				// this.hotid==='54d43437d47d13ff21cad58b'?false:true;
				if(this.hotid==="54d42d92321052167dfb75e3"){
					return false;
				}else if(this.hotid=="54d43437d47d13ff21cad58b"){
					return false;
				}else{
					return true;
				}
			}
		}
	}
</script>

<style lang="less">
	.BookItem{
		display: inline-block;
		vertical-align: top;
		white-space: normal;
		position: relative;
		.corner{
			width: 68px;
			padding: 12px 8px 8px;
		.corner-rank{
			font-size: 10px;
			position: absolute;
			overflow: hidden;
			width: 30px;
			height: 30px;
			text-align: center;
			em{
				font-size:9px;
				font-weight: 700;
				line-height: 10px;
				padding-top: 14px;
				text-transform: uppercase;
				position: absolute;
				right: -12px;
				bottom: 0;
				left: 0;
				transform: rotate(-45deg);
				transform-origin: left bottom;
				color: #fff;
			}
			&::after{
				position: absolute;
				top: 0;
				left: 0;
				width: 10px;
				height: 9px;
				content: '';
				background:url(data:image/svg+xml;base64,) no-repeat center;
				background-size: 100%;
			}
		}
		}
		img{
			display: block;
			width: inherit;
			height: 90px;
			box-shadow: 0 1px 3px rgba(0,0,0,.3);
		}
		.figcaption{
			font-size: 13px;
			line-height: 15px;
			overflow: hidden;
			max-height: 28px;
			margin: 8px 0 2px;			
		}
		.author{
			font-size: 12px;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			color: #969ba3;
			margin: 0;
		}
	}
	.rednum{
		background-color: #ed424b;
	}
	.orangenum{
		background-color: #f0643a;
	}
	.yellownum{
		background-color: #f0c53a;
	}
	.greynum{
		background-color: #969ba3;
	}
</style>
